import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MenuController, AlertController, LoadingController, NavController, ToastController, Platform, IonSlides } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';
import { DataService } from '../data.service';

@Component({
  selector: 'app-newhome',
  templateUrl: './newhome.page.html',
  styleUrls: ['./newhome.page.scss'],
})
export class NewhomePage {

  slideOpts = {
    initialSlide: 0,
    slidesPerView: 1,
    autoplay: true
  };

  prods: any =[];
  datastorage: any;
  name : any;
  uid: any;
  id: any = 0;
  device = true; 
  web = true;
  key: any =""; 

  constructor(
    private router: Router,
    private menuCtrl: MenuController,
    private dataService: DataService,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage, 
    private platform: Platform,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) {
    this.device = this.platform.is('mobile');
    this.web =this.platform.is('desktop');
    if(this.web==true){
      //this.menuCtrl.enable(false);
    };
   }

   
  ngOnInit() { 
    this.activatedRoute.params.subscribe((data : any)=>{
      this.id = data.id; 
      console.log(this.id)
     });
     
  }
  newhome(){
    this.router.navigate(['/newhome']);
  }

  async search() {
    if(this.key==""){
      this.presentToast('Please type keyword');
    }else{
      const loader = await this.loadingController.create({
        message: 'Please wait',
      });
      loader.present();

      this.navParamService.setKey(this.key);
      this.router.navigate(['/search']);
      loader.dismiss();
      }
    }
  ionViewWillEnter(){
    if(this.id == 1){
      window.location.reload();
    }
    this.storage.get('storage_xxx'). then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.uid=this.datastorage.user_id;
    });
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  

  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }

  async logout(){

    this.alertController
    .create({
      header: "Logout",
      message: "Are you sure you want to logout?",
      buttons: [
        {
          text: "Yes",
          handler: async ()=>{
            const loader = await this.loadingController.create({
              message: 'Please wait',
            });
            loader.present();
            loader.dismiss();
            this.storage.clear();
            this.presentToast('Log out successfully');
            this.navController.navigateRoot(['/intro']);
          }
        },
        { text: "No" }  
      ]
    })
    .then(alertEL => alertEL.present());

  }
  
  doRefresh(event) {
    console.log('Begin async operation');

    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }
  
 
  
}
